import FocusTrap from 'focus-trap-react';
import {Link} from 'gatsby';
import {StaticImage} from 'gatsby-plugin-image';
import isTouchDevice from 'is-touch-device';
import React, {useState} from 'react';

import Button from './common/Button';
import {MenuClose, RoundArrow} from './common/icons';

const MenuItem = ({link, number, name}) => (
  <Link to={link} className="flex items-start">
    <span className="text-xs font-mono text-primary mr-2 pt-1">{number}</span>
    {name}
  </Link>
);

export default ({isMenuOpened, setIsMenuOpened}) => {
  const [isTouch] = useState(isTouchDevice());

  return isMenuOpened ? (
    <div className="fixed inset-0 bg-n-500 text-n-100">
      <FocusTrap active={!isTouch}>
        <div className="fixed inset-0 overflow-y-auto">
          <div className="h-full min-h-620 pt-20 flex flex-col justify-between items-center">
            <div className="fixed top-0 px-10 md:px-24 md:max-w-screen-md py-4 w-full bg-n-500 flex justify-between items-center">
              <StaticImage src="../images/logo.png" alt="Elite Alliance" height={25} loading="eager" placeholder="none" />
              <button type="button" onClick={() => setIsMenuOpened(false)}>
                <MenuClose className="text-md" />
              </button>
            </div>
            <div className="flex-1 px-10 md:px-24 md:max-w-screen-md w-full box-border flex flex-col justify-center text-4xl space-y-10">
              <MenuItem link="/" number="01" name="About Us" />
              <MenuItem link="/" number="02" name="Contact" />
            </div>
            <div className="w-full px-10 md:px-24 md:max-w-screen-md">
              <Button className="w-full" rounded={false} icon={<RoundArrow className="transform -rotate-90" />}>Sign In</Button>
            </div>
          </div>
        </div>
      </FocusTrap>
    </div>
  ) : null;
};
